.w-100vh {
  min-width: 100vh;
}
.w-100 {
  width: 100%;
}
.h-100 {
  height: 100%;
}
.h-100vh {
  min-height: 100vh;
}
.text-light {
  color: @text-color-secondary;
  font-weight: 400;
  font-size: @font-size-sm;
}
.text-center {
  text-align: center;
}

.hanzi(@size, @val) {
  .hanzi-@{size} {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    align-content: space-around;
    font-size: @val / 4 * 3;
    width: @val;
    height: @val;
  }
}

.hanzi(lg, 64px);
.hanzi(md, 48px);
.hanzi(sm, 32px);
.hanzi(xs, 24px);

.d-flex {
  display: flex;
  align-items: center;
}

.f-wrap {
  flex-wrap: wrap;
}

.f-stretch {
  align-items: stretch;
}

.f-50 {
  flex-basis: 50%;
}

.f-33 {
  flex-basis: 33.333333%;
}

.f-25 {
  flex-basis: 50%;
}

.f-column {
  flex-direction: column;
}
.f-column-reverse {
  flex-direction: column-reverse;
}

.f-grow {
  flex-grow: 1;
  overflow: hidden;
}

.f-center {
  justify-content: center;
}

.f-space {
  justify-content: space-evenly;
}

.f-between {
  align-items: center;
  justify-content: space-between;
}

.scroll-auto {
  overflow: scroll;
}

.sticky {
  position: sticky;
}

.bg-light {
  background-color: @background-color-light;
}

.b-1 {
  border: solid 1px @border-color-base;
}

.b-primary {
  border-color: @primary-color;
}

.spacing(@property, @prefix, @size, @val) {
  
  .@{prefix}-@{size} { @{property}: @val; }
  .@{prefix}x-@{size} { @{property}-left: @val; @{property}-right: @val; }
  .@{prefix}y-@{size} { @{property}-top: @val; @{property}-bottom: @val; }
  .@{prefix}t-@{size} { @{property}-top: @val; }
  .@{prefix}b-@{size} { @{property}-bottom: @val; }
  .@{prefix}l-@{size} { @{property}-left: @val; }
  .@{prefix}r-@{size} { @{property}-right: @val; }
}

.spacing(padding, p, lg, @padding-lg);
.spacing(padding, p, md, @padding-md);
.spacing(padding, p, sm, @padding-sm);
.spacing(padding, p, xs, @padding-xs);
.spacing(padding, p, xss, @padding-xss);

.spacing(margin, m, lg, @margin-lg);
.spacing(margin, m, md, @margin-md);
.spacing(margin, m, sm, @margin-sm);
.spacing(margin, m, xs, @margin-xs);
.spacing(margin, m, xss, @margin-xss);
.spacing(margin, m, nlg, -@margin-lg);
.spacing(margin, m, nmd, -@margin-md);
.spacing(margin, m, nsm, -@margin-sm);
.spacing(margin, m, nxs, -@margin-xs);
.spacing(margin, m, nxss,- @margin-xss);